:root {
  --primary-color: #007AFF;
  --primary-light: rgba(0, 122, 255, 0.1);
  --primary-gradient: linear-gradient(45deg, #007AFF, #00C6FF);
  --text-dark: #333;
  --text-light: #666;
  --shadow-sm: 0 10px 30px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 20px 40px rgba(0, 0, 0, 0.2);
  --transition-standard: 0.3s ease;
  --overlay-bg: rgba(0, 122, 255, 0.9);
}

.projects-section {
  min-height: 100vh;
  padding: 6rem 2rem;
  background: var(--darker-bg);
  position: relative;
  overflow: hidden;
  color: rgba(255, 255, 255, 0.9);
  background: linear-gradient(135deg, #0a0a16 0%, #1a1a3a 100%);
}

.projects-section::before {
  content: '';
  position: absolute;
  inset: -50%;
  background-image:
    linear-gradient(rgba(0, 255, 255, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 255, 0.05) 1px, transparent 1px);
  background-size: 50px 50px;
  transform: perspective(500px) rotateX(60deg);
  animation: gridMove 20s linear infinite;
}

.projects-section::after {
  content: '';
  position: absolute;
  inset: 0;
  background-image: radial-gradient(circle at center, rgba(0, 255, 255, 0.1) 1px, transparent 1px);
  background-size: 40px 40px;
  animation: particleFloat 15s linear infinite;
}

.cyber-overlay {
  position: absolute;
  inset: 0;
  background:
    radial-gradient(circle at 20% 30%, rgba(176, 38, 255, 0.2) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(0, 255, 255, 0.2) 0%, transparent 50%);
  pointer-events: none;
}

@keyframes gridMove {
  0% {
    transform: perspective(500px) rotateX(60deg) translateY(0);
  }
  100% {
    transform: perspective(500px) rotateX(60deg) translateY(50px);
  }
}

@keyframes particleFloat {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-40px);
  }
}

.projects-content {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 3rem;
  font-family: var(--font-mono);
  position: relative;
}

.glitch-text {
  position: relative;
  color: var(--neon-purple);
  text-shadow: 0 0 10px rgba(176, 38, 255, 0.5);
}

.glitch-text::before,
.glitch-text::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.glitch-text::before {
  color: var(--neon-blue);
  z-index: -2;
  animation: glitch 4s steps(1) infinite;
}

.glitch-text::after {
  color: var(--neon-pink);
  z-index: -1;
  animation: glitch 4s steps(1) infinite reverse;
}

@keyframes glitch {
  0% {
    transform: translate(0);
  }
  2% {
    transform: translate(-3px, 1px);
  }
  4% {
    transform: translate(3px, -1px);
  }
  6% {
    transform: translate(0);
  }
  98% {
    transform: translate(0);
  }
  100% {
    transform: translate(0);
  }
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  padding: 1rem;
}

.project-card {
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 1rem;
  overflow: hidden;
  transform-style: preserve-3d;
  position: relative;
  backdrop-filter: blur(10px);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  background: rgba(0, 0, 0, 0.7);
}

.project-card:focus-within {
  box-shadow: 0 0 0 3px var(--neon-blue);
}

.project-image {
  position: relative;
  height: 200px;
  overflow: hidden;
  border-bottom: 1px solid var(--glass-border);
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-standard);
}

.project-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity var(--transition-standard);
  backdrop-filter: blur(4px);
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-card:hover .project-image img {
  transform: scale(1.1);
}

.project-links {
  display: flex;
  gap: 1.5rem;
}

.project-links a {
  color: white;
  font-size: 1.5rem;
  transition: transform var(--transition-standard);
}

.project-links a:hover {
  transform: translateY(-3px);
}

.project-info {
  padding: 2rem;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  border-top: 1px solid var(--glass-border);
}

.project-title {
  font-family: var(--font-mono);
  font-size: 1.5rem;
  color: rgb(0, 255, 255) !important;
  margin-bottom: 1rem;
  position: relative;
  display: inline-block;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.project-title::after {
  content: '';
  position: absolute;
  left: -10%;
  bottom: -5px;
  width: 120%;
  height: 1px;
  background: rgba(0, 255, 255, 0.2);
}

.project-description {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1.5rem;
  line-height: 1.6;
  font-size: 1.1rem;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1.5rem;
}

.tech-tag {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(0, 255, 255, 0.5);
  padding: 0.5rem 1rem;
  border-radius: 2rem;
  font-size: 0.9rem;
  font-family: var(--font-mono);
  border: 1px solid rgba(0, 255, 255, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  font-weight: 500;
}

.tech-tag:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--neon-blue);
  color: var(--neon-blue);
}

.project-link {
  color: var(--neon-blue);
  font-size: 1.5rem;
  transition: all 0.3s ease;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid var(--neon-blue);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.2);
}

.project-link:hover {
  transform: translateY(-2px);
  color: var(--neon-purple);
  border-color: var(--neon-purple);
  box-shadow: 0 0 15px var(--neon-purple);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }

  .project-card {
    max-width: 500px;
    margin: 0 auto;
  }
}

/* Hide decorative elements from screen readers */
.cyber-grid,
.cyber-noise,
.hologram-effect,
.card-scanline {
  pointer-events: none;
  user-select: none;
}

/* Reduced motion preferences */
@media (prefers-reduced-motion: reduce) {
  .hologram-effect,
  .card-scanline {
    animation: none;
  }

  .project-card {
    transition: none;
  }

  .glitch-text::before,
  .glitch-text::after {
    animation: none;
    display: none;
  }
}

/* High contrast mode adjustments */
@media (forced-colors: active) {
  .project-card {
    border: 2px solid CanvasText;
  }

  .tech-tag {
    border: 1px solid CanvasText;
  }

  .project-link {
    outline: 1px solid CanvasText;
  }
}

/* Add scanlines effect */
.project-card::after {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(
    transparent 50%,
    rgba(0, 255, 255, 0.015) 50%
  );
  background-size: 100% 4px;
  pointer-events: none;
  z-index: 2;
  opacity: 0.5;
}

/* Add glowing border on hover */
.project-card::before {
  content: '';
  position: absolute;
  inset: -1px;
  background: var(--cyber-gradient);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 1rem;
  z-index: -1;
}

.project-card:hover::before {
  opacity: 0.3;
}
