.contact-section {
  min-height: 100vh;
  padding: 6rem 2rem;
  background: var(--darker-bg);
  position: relative;
  overflow: hidden;
}

.contact-content {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 4rem;
  align-items: start;
}

.cyber-card {
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 4px;
  padding: 2rem;
  backdrop-filter: blur(10px);
  position: relative;
}

.cyber-card::before {
  content: '';
  position: absolute;
  inset: -2px;
  background: var(--cyber-gradient);
  border-radius: 6px;
  opacity: 0.5;
  z-index: -1;
}

.form-group {
  margin-bottom: 1.5rem;
  position: relative;
}

.cyber-label {
  display: block;
  font-family: var(--font-mono);
  color: var(--neon-blue);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  font-size: 0.9rem;
  letter-spacing: 1px;
}

.cyber-input,
.cyber-textarea {
  width: 100%;
  padding: 0.8rem;
  background: rgba(0, 255, 255, 0.05);
  border: 1px solid var(--neon-blue);
  border-radius: 4px;
  color: #fff;
  font-family: var(--font-mono);
  transition: all 0.3s ease;
}

.cyber-input:focus,
.cyber-textarea:focus {
  outline: none;
  box-shadow: 0 0 10px var(--neon-blue);
  background: rgba(0, 255, 255, 0.1);
}

.cyber-textarea {
  resize: vertical;
  min-height: 100px;
}

.cyber-button {
  position: relative;
  padding: 1rem 2rem;
  background: transparent;
  color: var(--neon-blue);
  font-family: var(--font-mono);
  text-transform: uppercase;
  letter-spacing: 2px;
  border: none;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease;
}

.cyber-button::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--neon-blue);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.cyber-button:hover::before {
  opacity: 0.1;
}

.cyber-button-border {
  position: absolute;
  inset: 0;
  border: 1px solid var(--neon-blue);
  transition: all 0.3s ease;
}

.cyber-button:hover .cyber-button-border {
  transform: translate(5px, 5px);
  box-shadow: -5px -5px 0 0 var(--neon-blue);
}

.social-links {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.social-link {
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 50%;
  color: var(--neon-blue);
  font-size: 1.2rem;
  transition: all 0.3s ease;
  position: relative;
}

.social-link:hover {
  color: var(--neon-purple);
  border-color: var(--neon-purple);
  text-shadow: var(--purple-glow);
}

.social-link::before {
  content: '';
  position: absolute;
  inset: -2px;
  border-radius: 50%;
  background: var(--cyber-gradient);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.social-link:hover::before {
  opacity: 0.5;
}

@media (max-width: 768px) {
  .contact-container {
    grid-template-columns: 1fr;
  }

  .social-links {
    flex-direction: row;
    justify-content: center;
    margin-top: 2rem;
  }
}

/* Accessibility Improvements */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

@media (prefers-reduced-motion: reduce) {
  .cyber-button,
  .social-link,
  .cyber-input,
  .cyber-textarea {
    transition: none;
  }
}

/* High Contrast Mode */
@media (forced-colors: active) {
  .cyber-input,
  .cyber-textarea,
  .cyber-button,
  .social-link {
    border: 2px solid CanvasText;
  }
}
