.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 1rem 2rem;
  transition: all 0.3s ease;
  z-index: 1000;
  background: rgba(10, 10, 22, 0.8);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--glass-border);
}

.header.scrolled {
  background: rgba(10, 10, 22, 0.95);
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.1);
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-link {
  text-decoration: none;
  position: relative;
}

.logo-text {
  font-family: var(--font-mono);
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--neon-blue);
  text-shadow: var(--neon-glow);
  letter-spacing: 1px;
}

.nav-links {
  display: flex;
  gap: 2rem;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-link {
  font-family: var(--font-mono);
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border: 1px solid transparent;
  border-radius: 4px;
}

.nav-link:hover {
  color: var(--neon-blue);
  text-shadow: var(--neon-glow);
  border-color: var(--neon-blue);
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.2);
}

@media (max-width: 768px) {
  .nav-links {
    gap: 1rem;
  }

  .nav-link {
    padding: 0.3rem 0.8rem;
    font-size: 0.9rem;
  }
}
