:root {
  --primary-color: #007AFF;
  --primary-gradient: linear-gradient(45deg, #007AFF, #00C6FF);
  --text-dark: #333;
  --text-light: #666;
  --transition-standard: 0.3s ease;
}

.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  background: var(--darker-bg);
}

.matrix-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
}

.cyber-noise {
  position: absolute;
  inset: 0;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.75' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  opacity: 0.05;
  pointer-events: none;
}

.hero-content {
  text-align: center;
  max-width: 800px;
  padding: 0 2rem;
  z-index: 1;
}

.hero h1 {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.greeting {
  font-family: var(--font-mono);
  font-size: 1.5rem;
  color: var(--neon-blue);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.name {
  font-size: clamp(2.5rem, 8vw, 4rem);
  font-weight: bold;
  color: var(--neon-blue);
  text-shadow: var(--neon-glow);
  position: relative;
}

.glitch-text {
  position: relative;
}

.glitch-text::before,
.glitch-text::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.glitch-text::before {
  animation: cyber-glitch-1 4s infinite;
  color: var(--neon-pink);
  clip: rect(44px, 450px, 56px, 0);
}

.glitch-text::after {
  animation: cyber-glitch-2 4s infinite;
  color: var(--neon-purple);
  clip: rect(44px, 450px, 56px, 0);
  left: 4px;
}

.title {
  font-size: clamp(1.5rem, 4vw, 2rem);
  color: var(--neon-purple);
  text-shadow: var(--purple-glow);
  font-family: var(--font-mono);
  position: relative;
}

.cyber-glitch {
  animation: text-flicker 4s infinite;
}

.subtitle {
  margin: 2rem 0;
  font-size: clamp(1rem, 2vw, 1.2rem);
  color: rgba(255, 255, 255, 0.8);
  font-family: var(--font-mono);
}

.cta-buttons {
  display: flex;
  gap: 2rem;
  justify-content: center;
}

.cyber-button {
  position: relative;
  padding: 1rem 2rem;
  background: transparent;
  color: var(--neon-blue);
  font-family: var(--font-mono);
  text-transform: uppercase;
  letter-spacing: 2px;
  text-decoration: none;
  overflow: hidden;
  transition: 0.3s;
}

.cyber-button.secondary {
  color: var(--neon-purple);
}

.cyber-button-border {
  position: absolute;
  inset: 0;
  border: 1px solid currentColor;
  transition: 0.3s;
}

.cyber-button:hover .cyber-button-border {
  transform: translate(5px, 5px);
  box-shadow: -5px -5px 0 0 currentColor;
}

.cyber-button::before {
  content: '';
  position: absolute;
  inset: 0;
  background: currentColor;
  opacity: 0;
  transition: 0.3s;
}

.cyber-button:hover::before {
  opacity: 0.1;
}

@keyframes cyber-glitch-1 {
  0% { clip: rect(132px, 350px, 101px, 30px); }
  20% { clip: rect(116px, 350px, 14px, 30px); }
  40% { clip: rect(61px, 350px, 127px, 30px); }
  60% { clip: rect(138px, 350px, 64px, 30px); }
  80% { clip: rect(48px, 350px, 103px, 30px); }
  100% { clip: rect(82px, 350px, 5px, 30px); }
}

@keyframes cyber-glitch-2 {
  0% { top: -1px; left: 1px; }
  20% { top: 1px; left: -1px; }
  40% { top: -1px; left: -1px; }
  60% { top: 2px; left: 2px; }
  80% { top: -2px; left: 1px; }
  100% { top: 1px; left: -1px; }
}

@keyframes text-flicker {
  0% { opacity: 1; }
  5% { opacity: 0.8; }
  10% { opacity: 1; }
  15% { opacity: 0.9; }
  20% { opacity: 1; }
  55% { opacity: 1; }
  60% { opacity: 0.7; }
  65% { opacity: 1; }
  70% { opacity: 0.9; }
  75% { opacity: 1; }
  100% { opacity: 1; }
}

@media (max-width: 768px) {
  .cta-buttons {
    flex-direction: column;
    padding: 0 2rem;
  }
}
