:root {
  /* Cyberpunk Color Palette */
  --neon-blue: #0ff;
  --neon-pink: #ff2d55;
  --neon-purple: #b026ff;
  --dark-bg: #0a0a16;
  --darker-bg: #050508;
  --cyber-gradient: linear-gradient(135deg, var(--neon-blue), var(--neon-purple));
  --glass-bg: rgba(255, 255, 255, 0.05);
  --glass-border: rgba(255, 255, 255, 0.1);

  /* Typography */
  --font-primary: 'Rajdhani', sans-serif;
  --font-mono: 'Share Tech Mono', monospace;

  /* Effects */
  --neon-glow: 0 0 10px rgba(0, 255, 255, 0.5),
               0 0 20px rgba(0, 255, 255, 0.3),
               0 0 30px rgba(0, 255, 255, 0.1);
  --purple-glow: 0 0 10px rgba(176, 38, 255, 0.5),
                 0 0 20px rgba(176, 38, 255, 0.3),
                 0 0 30px rgba(176, 38, 255, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
  overflow-x: hidden;
  background: var(--dark-bg);
  color: #fff;
}

code {
  font-family: var(--font-mono);
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}

/* Improve keyboard focus visibility */
:focus {
  outline: 2px solid var(--neon-blue);
  outline-offset: 2px;
}

/* Hide focus rings for mouse users, but keep them for keyboard */
:focus:not(:focus-visible) {
  outline: none;
}

/* Ensure focus is visible for keyboard users */
:focus-visible {
  outline: 2px solid var(--neon-blue);
  outline-offset: 2px;
  box-shadow: 0 0 10px var(--neon-blue);
}

/* Improve color contrast for text */
[data-text] {
  color: rgba(255, 255, 255, 0.9);
}

/* Ensure sufficient text contrast */
p, h1, h2, h3, h4, h5, h6 {
  color: rgba(255, 255, 255, 0.9);
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }

  .matrix-bg,
  .cyber-noise,
  .hologram-effect,
  .card-scanline {
    display: none;
  }
}

/* High contrast mode support */
@media (forced-colors: active) {
  * {
    border-color: CanvasText;
  }

  .cyber-button,
  .nav-link,
  .skill-card {
    border: 1px solid CanvasText;
  }
}
